.app {
  background: #020024;
  background: -webkit-gradient(linear, left top, right top, from(#020024), color-stop(20%, #121234), to(#00d4ff));
  background: linear-gradient(90deg, #020024 0%, #121234 20%, #00d4ff 100%);
  height: 749px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: white;
}
@media (max-width: 767px) {
  .app {
    width: 367px;
    height: 650px;
  }
}

.container {
  background-color: #121234;
  width: 700px;
  height: 500px;
  border-radius: 20px;
  border: 4px solid #000;
}
@media (max-width: 1024px) {
  .container {
    /*width: 420px;*/
    height: 450px;
    width: 600px;
  }
}
@media (max-width: 767px) {
  .container {
    height: 250px;
    width: 350px;
  }
}

.row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
